.chatPage {
    padding: 10px;
    height: 730px;
    width:18%;
    background: white;
    position: fixed;
    right: 0;
    bottom: 0;
    transform: translate(0px, 0px);
    transition: 0.5s;
    border: 1px solid silver;
    border-radius: 10px;
    .chatTop {
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 1px solid silver;
        div {
            font-size: 20px;
            span {
                margin: 0 3px 0 0;
            }
        }
        .icon {
            font-size: 30px;
            outline: 0px solid red;
        }
    }
    .chat {
        overflow: auto;
        height: 500px;
    }
    &.hide {
        transform: translate(0px, 675px);
    }
}

