.user {
    margin: 0 0 10px 0;
    img {
        width: 70px;
        height: 70px;
        border-radius: 50%;
    }
    .userInfo {
        display: flex;
        margin: 0 0 5px 0;
        .nameAndMess {
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            margin: 0 0 0 10px;
            .userName {
                font-size: 16px;
                font-weight: 700;
            }
            .message {
                display: flex;
                div {
                    margin: 0 5px 0 0;
                    font-weight: 500;
                }
            }
        }
    }
    .message {
        margin: 0 0 0 0;
    }
    .bottomLine {
        border-bottom: 1px solid silver;
    }
}